import react,{Component} from 'react';

class Product extends Component{
    render(){
        const{proimg,name,price,offer,designation}=this.props;
        return (
        <div className="container">
            <img src={proimg}/>
            <h3>{name}</h3>
            <p><b> {price}</b>  <b><strike>{offer}</strike></b></p>
            {/* <p><b>Designation {designation}</b></p> */}
        </div>
        );
    }
}

export default Product;